const search = document.getElementById('searchInput');
const resultParagraph = document.getElementById('instant-search__paragraph');

const books = [
  {
    title: 'Un chico y su perro en el fin del mundo',
    author: 'C.A. Fletcher',
  },
  {
    title: 'Un mundo feliz',
    author: 'Huxley',
  },
];

// search through books object
const searchBooks = () => {
  // Get matches to current text input
  let matches = books.filter((book) => {
    const regex = new RegExp(`^${searchBooks}`, 'gi');

    return book.title.match(regex) || book.author.match(regex);
  });
  console.log(matches);
  if (searchBooks.length === 0) {
    matches = [];
  }
  const outputHtml = (matches) => {
    const html = matches.map((match) => `<p>${match.title}</p>`).join('');
    resultParagraph.innerHTML = html;
  };
};
searchBooks();
//Add event listener
search.addEventListener('input', () => searchBooks(search.value));
